  height: 100%;
  width: 100%;
  overflow-x: hidden; }

body {
  scroll-behavior: auto;
  font-weight: inherit; }

* {
  border-spacing: 0px;
  box-sizing: border-box;
  margin: 0px;
  padding: 0px; }

html::-webkit-scrollbar {
  width: 6px;
  cursor: pointer; }

html::-webkit-scrollbar-track {
  border-radius: 10px; }

html::-webkit-scrollbar-thumb {
  background: #202d4d;
  border-radius: 10px; }

#main {
  position: relative;
  width: 100%;
  min-height: 100%;
  background: white; }

.hidden {
  display: none !important; }

.flex {
  display: flex !important; }

.display-unset {
  display: unset !important; }

.block {
  display: block !important; }

p {
  margin-block-start: unset;
  margin-block-end: unset; }
