  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap; }

@keyframes scroll {
  from {
    transform: translateX(0%); }
  to {
    transform: translateX(-50%); } }

.promo {
  position: relative;
  background: #FC0000; }
  .promo-head {
    position: relative;
    padding: 20px 60px 20px 60px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 14px; }
  .promo-logo {
    position: relative;
    width: 191px;
    height: 46px;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}); }
  .promo-options {
    position: relative; }
    .promo-options-item {
      position: relative;
      color: #F1F1F1;
      font-family: "Trial-Regular";
      font-size: 20px;
      margin-right: 30px;
      cursor: pointer; }
    .promo-options-item:nth-child(3) {
      margin-right: unset; }
  .promo-menu-btn {
    position: relative;
    width: 30px;
    height: 10px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    cursor: pointer;
    display: none; }
  .promo-text {
    position: relative;
    z-index: 0; }
  .promo-textBlock {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 0px;
    margin-top: 166px;
    animation: scroll 4s linear infinite; }
    .promo-textBlock-item {
      position: relative;
      -webkit-text-stroke: 1px #F1F1F1;
      text-stroke: 1px #F1F1F1;
      font-size: 120px;
      font-family: 'Trial-Bold-Italic';
      margin-right: 25px;
      line-height: 1; }
    .promo-textBlock-item:nth-child(odd) {
      color: #F1F1F1; }
    .promo-textBlock-item:nth-child(even) {
      color: #FC0000; }
  .promo-textBlock:nth-child(1) {
    margin-top: 316px;
    animation-direction: reverse; }
  .promo-video {
    margin: 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    padding-bottom: 124px;
    z-index: 1;
    position: relative; }
    .promo-video video {
      height: 584px; }
    .promo-video-item {
      position: relative;
      display: flex;
      justify-content: center; }
      .promo-video-item-btn {
        position: absolute;
        width: 136px;
        height: 136px;
        right: 0;
        margin-right: -68px;
        bottom: 0;
        margin-bottom: -68px;
        cursor: pointer; }
      .promo-video-item-img {
        position: relative;
        width: 438px;
        height: 584px;
        background-repeat: no-repeat;
        background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___}); }
